<template>
  <div class="circles">
    <div v-for="circle in circles" :key="circle.id" class="circle" :style="`background-color: ${circle.color}; top: ${circle.top}vh; left: ${circle.left}vw;`"></div>
  </div>
</template>

<script>
export default {
  name: 'App',
  data(){
    return{
      circles:[],
      circlesLen: 100
    }
  },
  methods:{
    getRandomColor() {
      var letters = '0123456789ABCDEF';
      var color = '#';
      for (var i = 0; i < 6; i++) {
        color += letters[Math.floor(Math.random() * 16)];
      }
      return color;
    },
    generateCircles(){
      for(let i = 0; i<this.circlesLen; i++){
        let circle = {
          id: i+1,
          color: this.getRandomColor(),
          top: Math.random() * 100,
          left: Math.random() * 100
        }
        this.circles.push(circle)
      }
    },
    changeCircles(){
      this.circles.forEach(item => {
        item.color = this.getRandomColor()
        item.top = Math.random() * 100
        item.left = Math.random() * 100
      })
    }
  },
  mounted(){
    this.generateCircles()
    setInterval(this.changeCircles, 500)
  }
}
</script>

<style>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;

  width: 100vw;
  height: 100vh;

  position: relative;
}
.circle{
  background-color: red;
  border-radius: 50%;

  width: 50px;
  height: 50px;

  position: absolute;
  transition: all .4s;
}
</style>
